import React from 'react';
import {Button, Div, List, Cell} from '@vkontakte/vkui'

class MyMatherialSelection extends React.Component {
    constructor(){
        super();
        this.state = {
            buttonId: null
            }
        this.setButton = this.setButton.bind(this);
        }
      setButton(id){
        this.setState({buttonId: id});
    }
    render() {
        return(
        <Div>
            <Div>
                <Button className={this.state.buttonId === 1?
                    "logoButton" : "Button"} 
                    size="xl"
                    stretched

                    level="2" 
                    onClick={() => this.setButton(1)}
                >САТИН</Button>
                <Button className={this.state.buttonId === 2?
                    "logoButton" : "Button"} 
                    stretched
                    size="xl"
                    level="2"
                    onClick={() => this.setButton(2)}
                >РЕПС</Button>
                <Button className={this.state.buttonId === 3?
                    "logoButton" : "Button"} 
                    size="xl"
                    stretched
                    level="2"
                    onClick={() => this.setButton(3)}
                >ХЛОПОК</Button>
                <Button className={this.state.buttonId === 4?
                    "logoButton" : "Button"} 
                    size="xl"
                    stretched
                    style={{ marginRight: 8 }}
                    level="2"
                    onClick={() => this.setButton(4)}
                >СИЛИКОН</Button>
                <Button className={this.state.buttonId === 5?
                    "logoButton" : "Button"} 
                    size="xl"
                    stretched
                    level="2"
                    onClick={() => this.setButton(5)}
                >КОЖКАРТОН</Button>
                <Button className={this.state.buttonId === 6?
                    "logoButton" : "Button"} 
                    size="xl"
                    stretched
                    level="2"
                    onClick={() => this.setButton(6)}
                >ПОЛИХЛОПОК</Button>
			</Div>

TODO:После нужно будет прижать поле с текстом по низу экрана, что бы не скакало и выглядело эстетичнее. Так же добавить ему собственный фон. Расставить Отступы на кнопках, что бы не сливались.
            <List>
                <Cell multiline>
                    {this.state.buttonId === 1 && " @Сатин@ Тонкий, мягкий, с лёгким блеском. Отлично передаёт четкие детали изображения, печать очень стойкая."}
                    {this.state.buttonId === 2 && " @Репс@ Плотный, рубчатый, фактурный материал, печать очень стойкая."}
                    {this.state.buttonId === 3 && " @Силикон@ Полупрозрачный, матовый и очень надежный материал, печать может быть только черного, синего, красного, зеленого, розового, золотого и серебряного цвета."}
                    {this.state.buttonId === 4 && " @Хлопок@ 100% натуральный хлопок полотняного плетения, печать выдерживает до 20 стирок."}
                    {this.state.buttonId === 5 && " @Кожкартон@ Печать лучше выполнять тёмными цветами, например, чёрным, красным или синим."}
                    {this.state.buttonId === 6 && " @Полихлопок@ Имитация хлопка. Рубчатый, плотный, фактурный материал. 100% ПЭ. Устойчив к стирке."}
                    {this.state.buttonId !== 1  &&
                        this.state.buttonId !== 2 &&
                        this.state.buttonId !== 3 &&
                        this.state.buttonId !== 4 &&
                        this.state.buttonId !== 5 &&
                        this.state.buttonId !== 6 && "Выбери свой вариант исполнения"}
                </Cell>
            </List>
        </Div>
        );
    }
}

export default MyMatherialSelection;