import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout} from '@vkontakte/vkui';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';
//import bumYarlik from '../img/bumYarlik.svg';
//import nakleyka from '../img/nakleyka@4x.svg';
//import prishivnaya from '../img/prishivnaya.svg';
//import termo from '../img/termo@4x.svg';
import MyTipeSelection from '../MyComponents/MyTipeSelection.js'
//Тут мы поставим несколько кнопок и при нажатии на однук из них будем выводить условным рендерингом InfoRow с информацией.
//https://webformyself.com/sozdanie-form-s-pomoshhyu-react/
//https://codepen.io/seraj/pen/mvMaJL


const TypeSelection = ({id, go}) => (
	<Panel id={id} style={{overflow: 'hidden'}}>
		<PanelHeader>
			ВЫБЕРИТЕ <strong>ТИП</strong>
		</PanelHeader>
		<Group className="Group1">
			<MyTipeSelection/>
			<FixedLayout vertical="bottom">
				<Div style={{display: 'flex'}}>
					<Button 
						className="Button" 
						size="xl"
						after={<img src={onLeft} alt="onLeft"/>}
						stretched 
						level="2" 
						onClick={go} 
						data-to="welcomeScreen"
					></Button>
					<Button 
						className="Button" 
						size="xl"
						after={<img src={onRight} alt="onRight"/>} 
						stretched 
						level="2" 
						onClick={go} 
						data-to="materialSelection"
					></Button>
				</Div>
			</FixedLayout>
		</Group>
	</Panel>
);


TypeSelection.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default TypeSelection;
