import React from 'react';
import {Button, Div, List, Cell} from '@vkontakte/vkui'
import bumYarlik from '../img/bumYarlik.svg';
import nakleyka from '../img/nakleyka@4x.svg';
import prishivnaya from '../img/prishivnaya.svg';
import termo from '../img/termo@4x.svg';


class MyTipeSelection extends React.Component {
    constructor(){
        super();
        this.state = {
            buttonId: null
            }
        this.setButton = this.setButton.bind(this);
        }
      setButton(id){
        this.setState({buttonId: id});
    }
    render(){
        return(
        <Div>
            <Button className={this.state.buttonId === 1?
                    "logoButton" : "Button"}  
                after={<img src={prishivnaya} alt="prishivnaya"/>}
                size="xl"
                stretched level="2"
                onClick={() => this.setButton(1)}
            >ПРИШИВНАЯ</Button>
            <Button className={this.state.buttonId === 2?
                    "logoButton" : "Button"}  
                after={<img src={bumYarlik} alt="bumYarlik"/>}
                size="xl"
                stretched level="2"
                onClick={() => this.setButton(2)}
            >БУМАЖНЫЙ ЯРЛЫК</Button>
            <Button className={this.state.buttonId === 3?
                    "logoButton" : "Button"}  
                after={<img src={termo} alt="termo"/>}
                size="xl"
                stretched level="2"
                onClick={() => this.setButton(3)}
            >ТЕРМО</Button>
            <Button className={this.state.buttonId === 4?
                    "logoButton" : "Button"}  
                after={<img src={nakleyka} alt="nakleyka"/>}
                size="xl"
                stretched level="2"
                onClick={() => this.setButton(4)}
            >НАКЛЕЙКА</Button>
TODO:Прижать поле с текстом вниз. Расставить Отступы на кнопках, что бы не сливались.
            <List>
                <Cell multiline>
                    {this.state.buttonId === 1 && " @Пришивная@ Сатин, репс, силикон, хлопок, кожкартон."}
                    {this.state.buttonId === 2 && " @Бумажный ярлык@ Белый или крафт-картон плотностью 250гр"}
                    {this.state.buttonId === 3 && " @Термонаклейка@ Термонаклейка на белом фоне, приклеивается утюгом, стойкая печать, несколько цветов на выбор"}
                    {this.state.buttonId === 4 && " @Наклейка@ Бумажная наклейка с полноцветной печатью, любой формы. Максимальный размер 9см"}
                    {this.state.buttonId !== 1  &&
                        this.state.buttonId !== 2 &&
                        this.state.buttonId !== 3 &&
                        this.state.buttonId !== 4 &&"Выбери свой вариант исполнения"}
                </Cell>
            </List>
        </Div>
        );
    }
}

export default MyTipeSelection;