import React from 'react';
import {FormLayout, Slider, Input} from '@vkontakte/vkui';

class MySlider extends React.Component {

    constructor (props) {
      super(props);

      this.state = {
        value1: 24.4234,
        value2: 24.4234,
      };
    }

    render() {
      return (
            <FormLayout>
                <Slider
                    min={10}
                    max={30}
                    value={Number(this.state.value1)}
                    onChange={value1 => this.setState({value1})}
                    top="Ширина, мм [10, 30]"
                />
                <Input value={String(this.state.value1)} onChange={e => this.setState({ value1: e.target.value })} type="number"/>
                <Slider
                    min={10}
                    max={30}
                    value={Number(this.state.value2)}
                    onChange={value2 => this.setState({value2})}
                    top="Длина, мм [10, 30]"
                />
                <Input value={String(this.state.value2)} onChange={e => this.setState({ value2: e.target.value })} type="number"/>
            </FormLayout>
      );
    }
  }

export default MySlider;